import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Embarazo from '../views/Embarazo.vue'
import Info from '../views/Info.vue'
import About from '../views/AboutUs.vue'
import Contact from '../views/Contact.vue'
import Familia from '../views/Familia.vue'
import Bodas from '../views/Bodas.vue'
import Glam from '../views/Glam.vue'
import Publicidad from '../views/Publicidad.vue'
import Retratos from '../views/Retratos.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/info',
    name: 'Info',
    component: Info
  },
  {
    path: '/embarazo',
    name: 'Embarazo',
    component: Embarazo
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact
  },
  {
    path: '/familia',
    name: 'Familia',
    component: Familia
  },
  {
    path: '/bodas',
    name: 'Bodas',
    component: Bodas
  },
  {
    path: '/glam',
    name: 'Glam',
    component: Glam
  },
  {
    path: '/retratos',
    name: 'Retratos',
    component: Retratos
  },
  {
    path: '/publicidad',
    name: 'Publicidad',
    component: Publicidad
  }
]
const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
