      :interval="3500"
      background="#ababab"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <!-- Text slides with image -->
      <b-carousel-slide :img-src="require('../assets/Carousel/1.jpg')"
      ></b-carousel-slide>
      <b-carousel-slide :img-src="require('../assets/Carousel/2.jpg')"
      ></b-carousel-slide>
      <b-carousel-slide :img-src="require('../assets/Carousel/3.jpg')"
      ></b-carousel-slide>
      <b-carousel-slide :img-src="require('../assets/Carousel/4.jpg')"
      ></b-carousel-slide>
       <b-carousel-slide :img-src="require('../assets/Carousel/5.jpg')"
      ></b-carousel-slide>
       <b-carousel-slide :img-src="require('../assets/Carousel/6.jpg')"
      ></b-carousel-slide>
