      ></b-carousel-slide>
    </b-carousel>
    <div class="logo"><img thumbnail fluid :src="require('../assets/YeiLetra.png')" alt="Image 3" width="80%" height="20%"></div>
  </div>
</template>

<script>
export default {
  name: 'Carousel',
  data () {
    return {
      slide: 0,
      sliding: null
    }
  },
  methods: {
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    }
  }
}
</script>
<style scoped>
.logo {
